(function() {
  'use strict';

  angular
    .module('webProject')
    .controller('adminPostController', adminPostController);

  /** @ngInject */
  function adminPostController($log, send, Notification) {
    var vm = this;
    vm.post = {};
    vm.posts = [];

    vm.getPosts = function(){
      send.request('/post', 'GET')
        .then(
          function(res) {
            vm.posts = res;
          },
          function(err) {
            $log.debug ('Error',err);
          }
        )
    }

    vm.edit = function(id){
      angular.forEach(vm.posts, function(entry) {
        if (entry.id === id)
          return vm.post = entry;
      })
    }

    vm.delete = function(id){
      $log.debug('sending Delete Request')
      send.request('/post/' + id, 'DELETE')
        .then(
          function(res) {
            if (res.message === 'Success'){
              Notification.success('Амжилттай устгалаа.');
              vm.getPosts()
            }
          }
        )
    }

    vm.submit = function(){
      var ok = false;
      if (vm.post.pic) ok = true;
      if (ok){
        if (vm.post.id){
          send.request('/post', 'PUT', vm.post, true)
            .then(
              function(res){
                if (res.message === 'Success'){
                  Notification.success('Амжилттай хадгаллаа.');
                  vm.post = {};
                  vm.getPosts();
                }
              },
              function(err){
                Notification.error('Хадгалалт амжилтгүй.');
                $log.debug(err);
              });
        } else {
          send.request('/post', 'POST', vm.post, true)
            .then(
              function(res){
                if (res.message === 'Success'){
                  Notification.success('Амжилттай хадгаллаа.');
                  vm.post = {};
                  vm.getPosts();
                }
              },
              function(err){
                Notification.error('Хадгалалт амжилтгүй.');
                $log.debug(err);
              });
        }
      }
      if (!ok) {
        Notification.error('Мэдээнд зураг сонгож өгнө үү');
      }
    }
    vm.new = function(){
      vm.post = {};
    }
    vm.getPosts();


  }
})();

(function() {
  'use strict';

  angular
    .module('webProject')
    .controller('adminParticipantController', adminParticipantController);

  /** @ngInject */
  function adminParticipantController($log, send, Notification) {
    var vm = this;
    vm.logo;
    vm.tours = [];
    vm.teams = [];
    vm.tour = {};

    vm.check = function(id){
      var ok = false;
      angular.forEach(vm.tour.participants, function(entry) {
        if (entry.team.id === id) {
          return ok = true;
        }
      })
      return ok;
    }

    vm.savePart = function(){
      send.request('/participant', 'POST', {
        tour_id: vm.tour.id,
        teams: vm.tour.participants
      }).then(
        function(res) {
          if (res.message === 'Success'){
            Notification.success('Амжилттай хадгаллаа.');
          }
        },
        function(err) {
          Notification.error('Хадгалалт амжилтгүй.');
          $log.debug(err);
        }
      )
    };

    vm.selectTour = function(id) {
      send.request('/tournament/' + id, 'GET')
        .then(
          function(res) {
            vm.tour = res;
          },
          function(err) {
            $log.debug(err);
          }
        )
    }

    vm.getTours = function(){
      send.request('/tournament', 'GET')
        .then(
          function(res) {
            vm.tours = res;
          },
          function(err) {
            $log.debug ('Error',err);
          }
        )
    }; vm.getTours();

    vm.getTeams = function(){
      send.request('/team', 'GET')
        .then(
          function(res) {
            vm.teams = res;
          },
          function(err) {
            $log.debug (err);
          }
        )
    }; vm.getTeams();

    vm.removeTeam = function(index){
      vm.tour.participants.splice(index, 1);
    }

    vm.pushTeam = function(id){
      angular.forEach(vm.teams, function(entry) {
        if (entry.id === id && !vm.check(entry.id)) return vm.tour.participants.push({ team: entry});
      })
    }
  }
})();

