(function() {
  'use strict';

  angular
    .module('webProject')
    .controller('AdminController', AdminController)
    .directive('fileModel', fileModel);


  /** @ngInject */
  function AdminController($log, send) {
    var vm = this;
    vm.post = {};

    vm.login = function(){
      send.request('/auth/local', 'POST', vm.user)
        .then(
          function(res){
            $log.debug('success', res);
          },
          function(err){
            $log.debug('error', err);
          });
    }
  }
  function fileModel ($parse){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        var model = $parse(attrs.fileModel);
        var modelSetter =  model.assign;

        element.bind('change', function(){
          scope.$apply(function(){
            modelSetter(scope, element[0].files[0]);
          })
        })
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('webProject')
    .run(runBlock)
    .filter('limitHtml', function() {
        return function(text, limit) {

            var changedString = String(text).replace(/<[^>]+>/gm, '');
            var length = changedString.length;

            return changedString.length > limit ? changedString.substr(0, limit - 1) : changedString;
        }
      })

  /** @ngInject */
  function runBlock($log, $rootScope, $state, redactorOptions, $cookies, config) {
    if (config.env === 'dev'){
      redactorOptions.imageUpload = config.dev.host + '/image';
    } else {
      redactorOptions.imageUpload = config.app.host + '/image';
    }


    // if (config.env === 'dev'){
    //   redactorOptions.imageUpload = config.dev.host + '/image?CSRF-TOKEN=' + $cookies.get('XSRF-TOKEN');
    // }
    // else {
    //   redactorOptions.imageUpload = config.app.host + '/image?CSRF-TOKEN=' + $cookies.get('XSRF-TOKEN');
    // }
    $rootScope.$on('$stateChangeError', function() {
      $state.go('login');
    })
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('webProject')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider
      .state('main', {
        abstract: true,
        url: '',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('main.homepage', {
        url: '/',
        templateUrl: 'app/main/homePage.html',
        controller: 'HomepageController',
        controllerAs: 'HH'
      })

      .state('admin', {
        url: '/admin',
        templateUrl: 'app/admin/admin.html',
        controller: 'AdminController',
        controllerAs: 'AA',
        resolve: {
          login: onlyAdmin
        }
      })
      .state('main.category', {
        url: '/category/:category',
        templateUrl: 'app/category/category.html',
        controller: 'CategoryController',
        controllerAs: 'CC'
      })
      .state('main.post', {
        url: '/post/:id',
        templateUrl: 'app/post/post.html',
        controller: 'PostController',
        controllerAs: 'PP'
      })

    $urlRouterProvider.otherwise('/');
    $locationProvider.html5Mode(true);
  }
  var onlyAdmin = function ($q, $log, send){
    var defer = $q.defer();
    send.request('/auth/local/', 'GET')
      .then(function(res) {
        if (res) defer.resolve();
      }, function(err) {
        if (err) defer.reject();
      })

    return defer.promise;
  }

})();

